.listMesseges {
  display: inline-block;
  width: 100%;
  overflow: hidden;
  overflow-y: auto;
  background: #f4a460;
  list-style-type: none;
  &::-webkit-scrollbar {
    width: 1em;
  }

  &::-webkit-scrollbar-track {
    box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
  }

  &::-webkit-scrollbar-thumb {
    background-color: #e0412a;
    outline: 1px solid #f4a460;
    border: 1px solid;
    }

  .message {
    background: #f4a460;
    .text-message {
      font-size: 2vh;
      .author-message {
        color: #f53232;
        font-size: 2.5vh;
        text-transform: uppercase;
      }      
      .time-message {
       color: #564479; 
       font-size: 1.5vh;
      }
    }
  }
}
