.input-user-name {
  font-family: 'Press Start 2P', cursive;
  font-size: 2vh;
  font-weight: normal;
  background: #f4a460;
  border: none;
  padding: 4px;
  text-align: center;
  margin-right: 10px;
  overflow: hidden;
  outline: none;
}

.submit-user-name {
  font-family: 'Press Start 2P', cursive;
  font-size: 1.5vh;
  background-color: #e0412a;
  border: 2px solid #000000;
  padding: 4px;
  color: #f4a460;
  text-transform: uppercase;
  cursor: pointer;
  outline: none;
  &:hover {
    color: #564479;
    background: #f4a460;
  }
  &:active {
    transform: scale(0.9);
  }
}
